<template>
  <dt-scroll-box>
    <div slot="top">
          <p class="dt-bread-item">项目申请</p>
    </div>
    <div slot="main-content">
       <div class="projectApply-page">
        <!-- 项目申报-项目申报入口界面 -->
        <!-- 项目申请标题 -->

        <!-- 表格 -->
        <el-table
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          :header-cell-style="{background:'#EBEBEB'}"
        >
          <!-- <el-table-column type="selection" width="55"></el-table-column> -->
          <!-- show-overflow-tooltip控制超过单元格的内容是否用...隐藏 -->
          <el-table-column prop="proType" label="项目类别" width></el-table-column>
          <el-table-column prop="bendTime" label="申请起止时间" width></el-table-column>
          <el-table-column prop="appCondition" label="申请条件" width="300" show-overflow-tooltip></el-table-column>

          <el-table-column label="操作">
            <template slot-scope="scope">
              <!-- 编辑按钮 -->
              <!-- <el-button size="mini" type="primary" icon="el-icon-edit" @click="hao()" circle></el-button>  -->
              <!-- 申请按钮 -->
              <span class="dt-txt-btn" @click="handleApply(scope.row)">申请</span>
            </template>
          </el-table-column>
        </el-table>
        <!-- 我的申请草稿箱 -->
        <p class="applytitle" style="marginTop:20px">我的申请(草稿箱)</p>
        <!-- 表格 -->
        <el-table
          :data="tableData1"
          tooltip-effect="dark"
          style="width: 100%"
          :header-cell-style="{background:'#EBEBEB'}"
          class="drafts"
        >
          <!-- <el-table-column type="selection" width="55"></el-table-column> -->
          <!-- show-overflow-tooltip控制超过单元格的内容是否用...隐藏 -->
          <el-table-column prop="proName" label="项目名称" show-overflow-tooltip width></el-table-column>
          <el-table-column prop="proType" label="项目类型" show-overflow-tooltip width></el-table-column>
          <el-table-column prop="proStatus" label="项目状态" show-overflow-tooltip></el-table-column>
          <el-table-column prop="proYear" label="项目年度" show-overflow-tooltip width></el-table-column>
          <el-table-column prop="subjectCate" label="学科分类" show-overflow-tooltip width></el-table-column>
          <el-table-column prop="unit" label="依托单位" show-overflow-tooltip width></el-table-column>
          <el-table-column prop="applyNum" class="blue" show-overflow-tooltip label="申请书编号" width></el-table-column>
          <el-table-column prop="budgeNum" class="blue" show-overflow-tooltip label="预算书编号" width></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <!-- 编辑按钮 -->
              <!-- <el-button size="mini" type="primary" icon="el-icon-edit" @click="hao()" circle></el-button>  -->
              <!-- 申请按钮 -->
              <span class="dt-txt-btn" @click="handleApply(scope.row)">删除</span>
              <span class="dt-txt-btn" style="marginLeft:10px" @click="handleApply(scope.row)">提交</span>
              <span class="dt-txt-btn" style="marginLeft:10px" @click="handleApply(scope.row)">立项</span>
            </template>
          </el-table-column>
        </el-table>

        <!-- 弹窗 -->
        <!-- <dt-sure-dialog description="这是一段描述哦!!!!"></dt-sure-dialog> -->
      </div>     
    </div>
  </dt-scroll-box>
</template>
<script>
/*
 *项目申报-项目申报入口界面
 * @author renchao 2019-02-13
 */
export default {
  data() {
    return {
      centerDialogVisible: false,
      // 表格
      tableData: [
        {
          proType: "院基科费",
          bendTime: "2018.01.21-2019.01.22",
          appCondition:
            "1.申请人为高级教授;2.申请人bulabulabulabulabulabulabulabula",
          routeName: "academyExpense"
        },
        {
          proType: "校基科费(教师)",
          bendTime: "2018.01.21-2019.01.22",
          appCondition:
            "1.申请人为高级教授;2.申请人bulabulabulabulabulabulabulabula",
          routeName: "schoolExpTeacher"
        },
        {
          proType: "校基科费(学生)",
          bendTime: "2018.01.21-2019.01.22",
          appCondition:
            "1.申请人为高级教授;2.申请人bulabulabulabulabulabulabulabula",
          routeName: "schoolExpStu"
        },
        {
          proType: "创新工程-重大协同创新",
          bendTime: "2018.01.21-2019.01.22",
          appCondition:
            "1.申请人为高级教授;2.申请人bulabulabulabulabulabulabulabula",
          routeName: "innovateCollaboration"
        },

        {
          proType: "创新工程-先导专项",
          bendTime: "2018.01.21-2019.01.22",
          appCondition:
            "1.申请人为高级教授;2.申请人bulabulabulabulabulabulabulabula",
          routeName: "innovateForerunner"
        },
        {
          proType: "创新工程-协同创意团队",
          bendTime: "2018.01.21-2019.01.22",
          appCondition:
            "1.申请人为高级教授;2.申请人bulabulabulabulabulabulabulabula",
          routeName: "innovateCreative"
        }
      ],
      tableData1: [
        {
          proName: "名称11111111",
          proType: "类型111111111",
          proStatus: "状态11111111",
          proYear: 2013,
          subjectCate: "分类11111111",
          unit: "单位11111111",
          applyNum: 11,
          budgeNum: 1313
        },
        {
          proName: "名称1",
          proType: "类型1",
          proStatus: "状态1",
          proYear: 2013,
          subjectCate: "分类1",
          unit: "单位1",
          applyNum: 11,
          budgeNum: 1313
        },
        {
          proName: "名称1",
          proType: "类型1",
          proStatus: "状态1",
          proYear: 2013,
          subjectCate: "分类1",
          unit: "单位1",
          applyNum: 11,
          budgeNum: 1313
        },
        {
          proName: "名称1",
          proType: "类型1",
          proStatus: "状态1",
          proYear: 2013,
          subjectCate: "分类1",
          unit: "单位1",
          applyNum: 11,
          budgeNum: 1313
        }
      ]
    };
  },
  methods: {
    //  跳转对应申请界面
    handleApply(row) {
      this.$router.push({
        name: row.routeName
      });
      // console.log(row);
    }
  }
};
</script>
<style lang="less">
.projectApply-page {
  padding-bottom: 60px;
  margin-top: 30px;
  .applytitle {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  @media screen and(max-width: 1600px) {
    .el-table {
      font-size: 14px !important;
    }
    .drafts {
      .dt-txt-btn {
        font-size: 12px;
        margin-left: 0 !important;
      }
    }
  }

  .drafts .el-table__row td:nth-child(n + 7) {
    color: #63b4ff;
  }
  //   表头

  //   表格
  .el-table {
    font-size: 16px;
    border: 1px solid #ebebeb;
  }
}
</style>





